import { useEffect } from 'react'
import { useState } from "react";
import Web3 from 'web3';
import avaxLogo from "./logos/avalanche-avax-logo.png"
const CoinGecko = require('coingecko-api');
const { MerkleTree } = require('merkletreejs');
const keccak256 = require('keccak256');

const Presale = () => {

    const [inputVal, newInputVal] = useState("");

    var userWalletAddress = null
    var amountAvax = 0

    useEffect(async() => {
        const loginButton = document.getElementById('connect-button')
        const sendButton = document.getElementById('allocation-button')
        if (!window.ethereum) {
            sendButton.style.background= "#4d4b4b";
            sendButton.disabled = true
            return false
          }
          window.web3 = new Web3(window.ethereum);
          const CoinGeckoClient = new CoinGecko();
          window.data = await CoinGeckoClient.coins.fetchTickers('avalanche-2', {});
          loginButton.addEventListener('click', loginWithMetaMask)
     }, []);

    async function loginWithMetaMask() {
        const loginButton = document.getElementById('connect-button')
        const sendButton = document.getElementById('allocation-button')
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' })
          .catch((e) => {
            console.error(e.message)
            return
          })
        if (!accounts) { return }
        userWalletAddress = accounts[0]
        loginButton.innerText = userWalletAddress.substring(0,6) + "..."
        loginButton.style.background = "#b13333"
        var balance = await window.web3.eth.getBalance(userWalletAddress)
        balance = parseFloat(balance) * 1e18;
        var val2 = balance.toString().split(".")[1]
        document.getElementById("user-balance").innerText = "Balance: " + balance.toString().split(".")[0] + "." + val2.slice(0, 2)

        sendButton.addEventListener('click', sendTx)
        loginButton.removeEventListener('click', loginWithMetaMask)
        setTimeout(() => {
          loginButton.addEventListener('click', signOutOfMetaMask)
        }, 200)
      }

      function signOutOfMetaMask() {
        const loginButton = document.getElementById('connect-button')
        const sendButton = document.getElementById('allocation-button')
        window.userWalletAddress = null
        loginButton.innerText = 'Connect'
        loginButton.style.background = "#0088a9";
        document.getElementById("user-balance").innerText = "Balance: 0"
      
        sendButton.removeEventListener('click', sendTx)
        loginButton.removeEventListener('click', signOutOfMetaMask)
        setTimeout(() => {
          loginButton.addEventListener('click', loginWithMetaMask)
        }, 200)
      }

      async function sendTx() {
        console.log(userWalletAddress)
        let whitelistAddresses = [
            "0xe397c5D7a8B704ffd03fa72DaB73b7fCb1514218",
            "0xAb8483F64d9C6d1EcF9b849Ae677dD3315835cb2",
            "0x4B20993Bc481177ec7E8f571ceCaE8A9e22C02db" ]
        const leafNodes = whitelistAddresses.map(addr => keccak256(addr));
        const merkleTree = new MerkleTree(leafNodes, keccak256, { sortPairs: true});
        const claimingAddress = keccak256(userWalletAddress).toString("hex")
        // `getHexProof` returns the neighbour leaf and all parent nodes hashes that will
        // be required to derive the Merkle Trees root hash.
        const hexProof = merkleTree.getHexProof(claimingAddress);
        const CONTRACT_ADDRESS = "0x6fdC1288607E291EFDce977Fa66DF7967B89Ef33"
        const contract = new window.web3.eth.Contract(window.ABI, CONTRACT_ADDRESS);
        const response = await contract.methods.requestAllocation(hexProof).send({from: userWalletAddress, value: window.web3.utils.toWei(amountAvax.toString(), "ether")})
      }

      async function avaxAmount(value) {
        checkRegex(value)
        var priced = value.target.value * window.data.data.tickers[0].last
        amountAvax = value.target.value.toString()
        console.log(amountAvax)
        var tokenAmount = window.data.data.tickers[0].last * amountAvax / 0.000005
        if(isNaN(tokenAmount)) {
            return
        }
        document.getElementById('to-amount').innerText = "$" + priced.toFixed(0)
        document.getElementById('user-get').innerText = tokenAmount.toFixed(0) + " AVPR"
      }

      window.ABI = [
        {
            "anonymous": false,
            "inputs": [
                {
                    "indexed": true,
                    "internalType": "address",
                    "name": "previousOwner",
                    "type": "address"
                },
                {
                    "indexed": true,
                    "internalType": "address",
                    "name": "newOwner",
                    "type": "address"
                }
            ],
            "name": "OwnershipTransferred",
            "type": "event"
        },
        {
            "inputs": [
                {
                    "internalType": "address",
                    "name": "_address",
                    "type": "address"
                }
            ],
            "name": "checkForAnormality",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "uint256",
                    "name": "_hardCap",
                    "type": "uint256"
                },
                {
                    "internalType": "uint256",
                    "name": "_maxBuy",
                    "type": "uint256"
                },
                {
                    "internalType": "uint256",
                    "name": "_minBuy_",
                    "type": "uint256"
                }
            ],
            "name": "modifyParameters",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "bytes32",
                    "name": "_merkleRoot",
                    "type": "bytes32"
                }
            ],
            "name": "newMerkleRoot",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "renounceOwnership",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "bytes32[]",
                    "name": "_merkleProof",
                    "type": "bytes32[]"
                }
            ],
            "name": "requestAllocation",
            "outputs": [],
            "stateMutability": "payable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "address",
                    "name": "newOwner",
                    "type": "address"
                }
            ],
            "name": "transferOwnership",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "address",
                    "name": "_address",
                    "type": "address"
                }
            ],
            "name": "checkAllocation",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "getHardCap",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "getMaxBuy",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "getMinBuy",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "merkleRoot",
            "outputs": [
                {
                    "internalType": "bytes32",
                    "name": "",
                    "type": "bytes32"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "owner",
            "outputs": [
                {
                    "internalType": "address",
                    "name": "",
                    "type": "address"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        }
    ]

      function checkRegex(e) {
        newInputVal(e.target.value.replace(/[^\d.]+/g, ""));
    }

    

    return (          
        <div>
            <div className="top-text">
                <h1>AvPrint Presale</h1>
                <h2 className="h2">Welcome to the presale page. You're able to allocate up to $700 in AVAX for a price of $0.000005 each.</h2>
            </div>
            <div className="main-container glass">
                <div className="top-container glass">
                    <input type="text" name="" id="amount-input" value={inputVal} minLength={"1"} maxLength={"4"} onChange={avaxAmount} />
                    <p className="to-amount" id="to-amount">$0</p>
                    <img src={avaxLogo} alt="avax-logo" />
                    <p className="crypto">AVAX</p>
                    <p className="user-balance" id="user-balance">Balance: 0</p>
                </div>
                <svg className="down-arrow" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                    <path fill="#FFF" d="M256 0C114.6 0 0 114.6 0 256c0 141.4 114.6 256 256 256s256-114.6 256-256C512 114.6 397.4 0 256 0zM382.6 302.6l-103.1 103.1C270.7 414.6 260.9 416 256 416c-4.881 0-14.65-1.391-22.65-9.398L129.4 302.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L224 306.8V128c0-17.69 14.33-32 32-32s32 14.31 32 32v178.8l49.38-49.38c12.5-12.5 32.75-12.5 45.25 0S395.1 290.1 382.6 302.6z"/>
                </svg>
                <div className="bottom-container glass">
                    <p className="user-get" id="user-get"></p>
                </div>
                <button className="connect-button" id="connect-button">Connect</button>
                <button className="allocation-button" id="allocation-button">Request Allocation</button>
            </div>
            <script src="init.js"></script> 
        </div>
        
     );
}

// 
 
export default Presale;