import { Link } from 'react-router-dom';
import Logo from './logos/website-logo.png';

const Navbar = () => {

    function menuManager() {
        const menuBtn = document.querySelector('.menu-btn');
        const navLinks = document.querySelector('.nav__links');
        menuBtn.classList.toggle("open");
        navLinks.classList.toggle("open");
    }
    return ( 
        <header>
            <img className='website-logo' src={Logo} alt="Logo"/>
            <h1 className="nav__title"><Link to="/" style={{fontSize: 32, fontWeight: 700}}>AvPrint</Link></h1>
            <div className="menu-btn" onClick={menuManager}>
                <div className="menu-btn__burger"></div>
            </div>
            <nav>
                <ul className="nav__links">
                    <li><a href="https://medium.com/@avprint/how-avprint-works-d99a0788cde7" target={"_blank"}>WHITEPAPER</a></li> 
                    <li><Link to="#">ROADMAP</Link></li>
                    <li><a href="https://twitter.com/AvPrintFinance" target={"_blank"}>TWITTER</a></li>
                    <li><Link to="/presale"><button>DAPP</button></Link></li>
                </ul>
            </nav>
        </header>

    );

    
}
 
export default Navbar;